@import '../index';

.bar {
    width: 100%;
    height: 8px;
    background-color: var(--clr-navyBlue-dark-tr);
    position: absolute;

    bottom: 0;
}
header {
    position: relative;
    overflow: hidden;
    padding: 2.5rem;
    padding-inline: 1.5rem;
    display: grid;
    place-items: center;

    @media only screen and (max-width: 380px) {
        & {
            --fs-hero: 3rem;
        }
    }
}

.backgroundImage,
.backgroundColor {
    position: absolute;
    inset: -100px;
    z-index: -100;
    background-color: var(--clr-whitePink-tr55);
}

.backgroundImage {
    --rotate: 45deg;
    background-color: transparent;
    background-image: url(../img/hero-background.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    animation: shake 12s ease-in-out;
    animation-iteration-count: infinite;

    @media only screen and (min-width: 855px) {
        & {
            background-image: url(../img/hero-background-2x.png);
        }
    }
                                @media only screen and (min-width: 520px) {
                                    & {
                                        --rotate: 5deg;
                                    }
                                }
}

.container {
    @include text-bold;
    line-height: normal;
    display: flex;
    flex-direction: column;
    font-size: var(--fs-sm);
}

.top {
    color: var(--clr-gray-600);
    transform: translate(3px, 5px);
}

.main {
    font-size: var(--fs-hero);
    color: var(--clr-navyBlue-light);
    line-height: 90%;

    .number {
        color: var(--clr-skyBlue-dark);
        font-size: calc(var(--fs-hero) + 5px);
        letter-spacing: -3px;
        text-shadow:
            -2px -2px 0px var(--clr-navyBlue-dark),
            -1px -2px 0px var(--clr-navyBlue-dark),
            0px -2px 0px var(--clr-navyBlue-dark),
            1px -2px 0px var(--clr-navyBlue-dark),
            2px -2px 0px var(--clr-navyBlue-dark),
            2px -1px 0px var(--clr-navyBlue-dark),
            2px 0px 0px var(--clr-navyBlue-dark),
            2px 1px 0px var(--clr-navyBlue-dark),
            2px 2px 0px var(--clr-navyBlue-dark),
            1px 2px 0px var(--clr-navyBlue-dark),
            0px 2px 0px var(--clr-navyBlue-dark),
            -1px 2px 0px var(--clr-navyBlue-dark),
            -2px 2px 0px var(--clr-navyBlue-dark),
            -2px 1px 0px var(--clr-navyBlue-dark),
            -2px 0px 0px var(--clr-navyBlue-dark),
            -2px -1px 0px var(--clr-navyBlue-dark),
            5px 5px 0 var(--clr-navyBlue-dark);
    }
}

.bottom {
    color: var(--clr-gray-600);
    align-self: flex-end;
    margin-top: 10px;

    .em {
        color: var(--clr-gray-900);
    }
}


@keyframes shake {
    0% {
        transform: translate(1px, 1px) rotate(calc(var(--rotate) + 0deg));
    }

    10% {
        transform: translate(-1px, -2px) rotate(calc(var(--rotate) - 1deg));
    }

    20% {
        transform: translate(-3px, 0px) rotate(calc(var(--rotate) + 1deg));
    }

    30% {
        transform: translate(3px, 2px) rotate(calc(var(--rotate) + 0deg));
    }

    40% {
        transform: translate(1px, -1px) rotate(calc(var(--rotate) + 1deg));
    }

    50% {
        transform: translate(-1px, 2px) rotate(calc(var(--rotate) - 1deg));
    }

    60% {
        transform: translate(-3px, 1px) rotate(calc(var(--rotate) + 0deg));
    }

    70% {
        transform: translate(3px, 1px) rotate(calc(var(--rotate) - 1deg));
    }

    80% {
        transform: translate(-1px, -1px) rotate(calc(var(--rotate) + 1deg));
    }

    90% {
        transform: translate(1px, 2px) rotate(calc(var(--rotate) + 0deg));
    }

    100% {
        transform: translate(1px, 1px) rotate(calc(var(--rotate) + 0deg));
    }
}