@import '../index.scss';

.container {
    --height: clamp(130px, 34.48vw, 300px);
    --font-size-p: clamp(1.2rem, 3.6vw, 2rem);
    --font-size-s: clamp(.6rem, 1.47vw, .8rem);
    --margin-top: clamp(.8rem, 3.6vw, 2rem);
    height: var(--height);
    background-color: var(--clr-navyBlue-dark);
    margin-top: clamp(4rem, 14vw, 8rem);

    @media only screen and (max-width: 410px) {
        .textContainer {
            transform: translateY(-30%);

        }

    }
}

.wrapper {
    @include max-width-md;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.imgWrapper {
    display: flex;
    transform: translateY(-20%);

    img {
        max-width: 100%;
    }
}

.textContainer {
    font-family: var(--ff-footer);
    align-self: center;
    display: flex;
    flex-direction: column;
    transform: translateY(-35%);
    padding-right: 1rem;

    .primary {
        // font-size: 2rem;
        // font-size: 1.2rem;
        font-size: var(--font-size-p);
        line-height: 20px;
        color: var(--clr-skyBlue);
        font-weight: 700;
    }

    .secondary {
        color: var(--clr-gray-600);
        font-family: var(--ff-footer-s);
        margin-top: clamp(1px, 1.15vw, 10px);
        font-size: var(--font-size-s);
        font-weight: 400;
    }

    .sign {
        font-family: var(--ff-sign);
        color: var(--clr-white);
        text-align: center;
        // margin-top: 2rem;
        // margin-top: 1rem;
        margin-top: var(--margin-top);
    }
}