@import '../index.scss';

.container {
    @include max-width-md;
    @include text-bold;
    line-height: normal;

    display: flex;
    flex-direction: column;
    gap: 3rem;

    margin-top: 3rem;
    padding-inline: clamp(1.5rem, 6.67vw, 2.5rem);

    @media only screen and (max-width: 380px) {
        & {
            --fs-big: 1.5rem;

            h1 {
                max-width: 180px;
            }
        }
    }
}

h1 {
    --radius: clamp(1rem, 4.6vw, 2rem);
    font-size: var(--fs-big);
    max-width: 550px;
    max-width: clamp(280px, 69.62%, 550px);
    position: relative;
}

@mixin after {
    content: '';
    position: absolute;
    width: clamp(40px, 11.75vw, 100px);
    height: 2.5em;
    border: 2px dashed var(--clr-navyBlue-light);
    bottom: 0;
    border-top: 0;
    transform: translate(0, 120%);
}

.qs_01 {
    &::after {
        @include after;
        border-right: 0;
        left: 10%;
        border-radius: 0 0 0 var(--radius);
    }

    .vector {
        position: absolute;
        // max-width: 70px;

        max-width: clamp(40px, 14vw, 150px);
        left: 100%;
        top: 50%;
        transform: translate(30%, -50%);

        @media only screen and (max-width: 420px) {
            & {
                top: 100%;
                left: 90%;
                transform: translate(-50%, -50%);
            }
        }

        @media only screen and (max-width: 380px) {
            & {
                display: none;
            }
        }
    }
}

.qs_02 {
    align-self: flex-end;
    text-align: end;

    &::after {
        @include after;
        left: 70%;
        border-left: 0;
        border-radius: 0 0 var(--radius) 0;
    }
}

.qs_03 {
    max-width: clamp(240px, 51.4%, 450px);
}