*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

//*google fonts
@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap'); //Kaushan Script
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap'); //Roboto Condensed
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;700&display=swap'); //Oswald
@import url('https://fonts.googleapis.com/css2?family=Finlandica&display=swap'); //Finlandica
// font-family: 'Roboto Condensed', sans-serif;
// font-family: 'Kaushan Script', cursive;
// font-family: 'Oswald', sans-serif;
// font-family: 'Finlandica', sans-serif;

//*custom variables
:root {
    //colors
    --clr-navyBlue-light: #435765;
    --clr-navyBlue-dark: #1B445F;
    --clr-navyBlue-dark-tr: #1b445f94;
    --clr-skyBlue-light: #DAEAEF;
    --clr-skyBlue: #BAD7E0;
    --clr-skyBlue-dark: #A0DFF2;

    --clr-whitePink: #F9F1F1;
    --clr-whitePink-tr55: #f9f1f177;

    --clr-gray-300: #D9D9D9;
    --clr-gray-600: #B3B3B3;
    --clr-gray-900: #737373;

    --clr-black: black;
    --clr-white: white;
    --clr-bg: #EDEDED;

    //font-size
    --fs-sm-xs: .5rem;
    --fs-sm: clamp(1.1rem, 3.2vw, 1.5rem);
        --fs-medium: clamp(1.5rem, 3.2vw, 2rem);
        --fs-normal: clamp(1.5rem, 4.45vw, 2.5rem);
    --fs-big: clamp(2rem, 7.11vw, 4rem);
    --fs-hero: clamp(5rem, 19vw, 7rem);

    //font-family
    --ff-primary: 'Roboto Condensed', sans-serif;
    --ff-footer: 'Oswald', sans-serif;
    --ff-footer-s: 'Finlandica', sans-serif;
    --ff-sign: 'Kaushan Script', cursive;

    // spacings
    --padding-X-body: 1.5rem;
    --padding-x: 1rem;
    --padding-Y: 1rem;
    --padding-Y-l: 2rem;

    --margin-Y-xs: .5rem;
    --margin-Y-s: 2rem;
        --margin-Y: 2.5rem;
}

// * controled tags
body {
    background-color: var(--clr-bg);
    overflow-x: hidden;
}

//*utility mixins
//text =>
@mixin text-regular {
    font-family: var(--ff-primary);
    font-weight: 300;
}

@mixin text-medium {
    font-family: var(--ff-primary);
    font-weight: 400;
    line-height: 150%;
}

@mixin text-bold {
    font-family: var(--ff-primary);
    font-weight: 700;
    line-height: 150%
}

@mixin text-typography {
    font-family: var(--ff-primary);
    font-weight: 300;
    line-height: 150%
}

@mixin max-width {
    max-width: 1024px;
    margin-inline: auto;
}
@mixin max-width-md {
    max-width: 850px;
    margin-inline: auto;
}

//utility classes
.accent {
    color: var(--clr-navyBlue-dark);
}