@import '../index.scss';

.container {
    @include text-medium;
    line-height: normal;
    background-color: var(--clr-navyBlue-dark);
    color: var(--clr-whitePink);
    position: relative;

    margin-top: 6rem;
    padding: clamp(1.5rem, 11vw, 3rem);

    &::after {
        content: '';
        position: absolute;
        width: clamp(70px, 12vw, 150px);
        // width: 70px;
        aspect-ratio: 1/1;
        background-image: url(../img/circle.svg);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        top: 0;
        left: 10%;
        transform: translateY(-50%);
    }

    @media only screen and (max-width: 350px) {
        & {
            --fs-normal: 1.3rem;

            &::after {
                width: 50px;
            }
        }
    }
}

.text {
    max-width: 650px;
    font-size: var(--fs-normal);
    border: 2px dashed var(--clr-skyBlue);
    margin-inline: auto;
    padding: 1.2rem;
    text-align: center;
}