@import '../index.scss';

.textContainer {
    @include max-width-md;
    display: flex;
    flex-direction: column;
    padding-inline: 2rem;
}

.boldTitleContainer {
    margin-top: 4rem;
    margin-bottom: .5rem;
    display: flex;
    justify-content: center;

    h1 {
        @include text-bold;
        font-size: var(--fs-sm);
    }

    span {
        color: var(--clr-navyBlue-dark);
        position: relative;

        &::after {
            content: 'May';
            position: absolute;
            color: var(--clr-skyBlue-dark);
            inset: 0;
            z-index: -1;
            transform: translate(1.5px, -1px);
        }
    }
}

.regularTextContainer,
.lightTextContainer {
    @include text-medium;
    margin-top: var(--margin-Y);
    font-size: var(--fs-medium);
    text-align: center;
    text-align: justify
}

.lightTextContainer {
    @include text-regular
}

.formula {
    @include text-bold;
    width: clamp(320px, 72vw, 500px);
    font-size: var(--fs-normal);
    margin-block: 4rem;
    padding-inline: 1.5rem;
    text-align: center;
    align-self: center;

    p {
        border: 2px dashed var(--clr-navyBlue-dark);
        padding: 1rem;
    }
}

.boldTextContainer {
    @include text-medium;
    font-size: var(--fs-medium);
    margin-top: var(--margin-Y);
    text-align: justify;
}

.ogPostContainer {
    box-shadow: 0 0 0px 13px var(--clr-navyBlue-dark);
    outline: 3px solid var(--clr-skyBlue-dark);
    outline-offset: 5px;
    padding-inline: 1rem;
    padding-block: 2rem;
    margin-bottom: 2rem;

    .boldTitleContainer {
        margin: 0;
    }

    .imgContainer {
        padding: 0;
        display: flex;
        margin-top: var(--margin-Y);


        img {
            max-width: 100%;
        }
    }

    .regularTextContainer {
        max-width: 650px;
        padding-inline: 1rem;

        margin-top: 2rem;
        margin-inline: auto;

    }
}

.indentedBold {
    padding-inline: 2rem;
}


.imgContainer {
    max-width: 700px;
    display: flex;
    margin-inline: auto;
    margin-top: var(--margin-Y);
    flex-direction: column;
    gap: var(--margin-Y-s);
    padding-inline: 1rem;


    img {
        max-width: 100%;
        box-shadow: rgba(0, 0, 0, 0.38) 0px 25px 20px -20px;
        border: 5px solid var(--clr-navyBlue-dark);
        border-radius: 5px;
    }
}

.center {
    margin-inline: auto;
}

.small {
    @include text-bold;
    // display: none;
    font-size: var(--fs-sm);
}